import React from "react";
import { Formik, Form, Field } from "formik";

import "../style.css";
import firestore from "./firebase/firebaseAuth";

const CodeInput = class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      validity: "",
      error: false,
    };
  }
  handleSubmit = (code) => {
    if (!code) return;

    if (this.isValid(code)) {
      console.log("This is a valid code");
      this.setState({ validity: "valid" });
      this.sendToDB(code);
    } else {
      this.setState({ validity: "invalid" });
      console.log("Invalid, please check your code");
    }
  };

  isValid = (code) => {
    const check = /(^[0-9]*$)/;
    return check.test(code) && code.length === 12;
  };

  setColorStyle = () => {
    if (!this.state.validity) return "#0004";
    if (this.state.validity === "valid" && !this.state.error) return "#4a0a";
    return "#f34a";
  };

  sendToDB = (code) => {
    firestore
      .collection("codes")
      .add({
        code: code,
        timestamp: new Date(),
      })
      .then(() => {
        this.setState({ error: false });
      })
      .catch(() => {
        this.setState({ error: true });
      });
  };

  render() {
    return (
      <div
        className="container inputArea"
        style={{ backgroundColor: this.setColorStyle() }}
      >
        <Formik
          initialValues={{ code: "" }}
          onSubmit={(obj) => this.handleSubmit(obj.code)}
        >
          <Form className="inputForm">
            <Field id="code" name="code" placeholder="Code" />
            <br />
            <button type="submit">Add</button>
          </Form>
        </Formik>
        {this.state.validity ? (
          this.state.validity === "valid" ? (
            this.state.error === false ? (
              <p>Code Submitted!</p>
            ) : (
              <p>There was an error, please try again</p>
            )
          ) : (
            <p>Invalid Code, try again</p>
          )
        ) : (
          <p>Enter your code above</p>
        )}
      </div>
    );
  }
};

export default CodeInput;
