import React from "react";
import firestore from "./firebase/firebaseAuth";

const CodeList = class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      codeList: [],
    };
    firestore
      .collection("codes")
      .limit(10)
      .orderBy("timestamp", "desc")
      .get()
      .then((querySnapshot) => {
        querySnapshot.docs.forEach((doc) => {
          if (this.state.codeList.includes(doc.data())) return;

          this.setState({
            codeList: [...this.state.codeList, doc.data().code],
          });
        });
      });
  }

  render() {
    console.log(this.state.codeList);
    return (
      <div className="container listArea">
        <p>Other codes:</p>
        {this.state.codeList.map((code) => {
          return <p>{code}</p>;
        })}
      </div>
    );
  }
};

export default CodeList;
